body {
    font-family: "Open Sans", sans-serif;
    font-weight: 300;
}

main {
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f8f8f8;
    color: #2f2f2f;
}

.main-info,
.times {
    box-sizing: border-box;
    padding: 35px;
    border-radius: 5px;
    border: 1px solid #fefefe;
    background-color: #fff;
    box-sizing: border-box;
    min-width: 320px;
    height: 310px;
    margin: 0 20px;
    position: relative;
}

.main-info__city,
.times__title,
form .form__title {
    font-weight: 400;
    font-size: 26px;
}

.main-info__degrees,
.times__degrees {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.main-info__val,
.times__val {
    font-size: 35px;
}

.main-info__table,
.times-item__table {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.main-info-table__item,
.times-item-table__item {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 10px;
}

.times {
    position: relative;
}

.times-pagination {
    position: absolute;
    bottom: -45px;
    left: 0;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    box-sizing: border-box;
    padding: 0 35px;
}

.times-pagination button {
    display: block;
    padding: 0;
    border: 1px solid #2f2f2f;
    background-color: transparent;
    cursor: pointer;
    padding: 5px;
    position: relative;
    margin-right: 5px;
    min-width: 30px;
}

.times-pagination button:focus {
    outline: none;
}

.times-pagination button.active {
    content: "";
    background-color: #2f2f2f;
    color: #fff;
}

.times-pagination button.disabled {
    opacity: 0.5;
    border: 1px solid #cacaca;
    pointer-events: none;
}

.form-container {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding: 15px;
    transition: all 0.2s ease;
    background-color: rgb(0 0 0 / 70%);
}

form {
    background-color: #fff;
    padding: 50px;
    margin-top: 50px;
    position: relative;
    top: 0;
    transition: top 0.2s ease;
}

form input {
    width: 100%;
    margin: 15px 0;
    padding: 5px;
    box-sizing: border-box;
    border: 1px solid #2f2f2f;
    height: 35px;
    transition: all 0.2s ease;
}

form input.invalid,
form input.invalid:focus {
    border: 1px solid #fb5151;
}

form input:focus,
form button:focus {
    outline: none;
    border-color: #226aff;
    box-shadow: inset 0 2px 1px rgba(0, 0, 0, 0.1235);
}

form button {
    cursor: pointer;
    background-color: #226aff;
    border: none;
    color: #fff;
    height: 35px;
    line-height: 35px;
    padding: 0 10px;
    transition: background-color 0.2s ease;
}

form button:hover {
    background-color: #558dff;
}

.form__alert {
    margin: 10px 0;
    color: #fb5151;
    font-weight: 400;
    display: none;
}

.form__alert.show {
    display: block;
}

.loading {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
}

@media screen and (max-width: 720px) {
    main {
        flex-direction: column;
    }

    .main-info,
    .times {
        margin: 20px 0;
    }

    .form-container {
        align-items: center;
    }

    form {
        margin: 0;
    }
}

@media screen and (max-width: 540px) {
    form {
        padding: 25px;
    }

    form .form__title {
        font-size: 21px;
    }
}

